.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.social-buttons {
  display: flex;
  gap: 10px;
}
.social-buttons .social-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: #337d5a;
  color: #ffffff;
  font-size: 17px;
  width: 31px;
  height: 31px;
}
.social-buttons .social-button:hover {
  background: #00572e;
}
.social-buttons .social-button:active {
  background: none;
  color: #337d5a;
  border: 1px solid #337d5a;
}
.social-buttons .social-button.icon-telegram::before {
  font-size: 14px;
  margin-left: -3px;
}
.social-buttons .social-button.icon-whatsapp::before {
  font-size: 15px;
  margin-left: 2px;
}